.app-name {
  font-family: 'Bebas Neue', cursive;
  text-align: center;
  font-size: 4em;
  color: white;
  text-shadow: 5px 5px 0 black;
  margin: 0;
  margin-top: .5rem;
}

header {
  border-bottom: 4px solid #F9D03F;
  -webkit-box-shadow: 0px 10px 10px black;
          box-shadow: 0px 10px 10px black;
  margin: 0;
}

@media screen and (max-width: 480px) {
  .app-name {
    font-size: 3rem;
    text-align: start;
    margin-left: 1rem;
  }
}


